	module.exports = {"data":{"markdownRemark":{"html":"<p>In today's topic, we will cover the details you need to know to make the decision.</p>\n<p>Whether to use</p>\n<pre><code>create-react-native-app MyAwesomeApp\n</code></pre>\n<p>or</p>\n<pre><code>exp init HelloExpo\n</code></pre>\n<p>or</p>\n<pre><code>react-native init VanillaRN\n</code></pre>\n<p><code>create-react-native-app</code> (CRNA) was <a href=\"https://facebook.github.io/react-native/blog/2017/03/13/introducing-create-react-native-app.html\">introduced to the community</a> on March 13, 2017.\nWith the intention to make developers life easier without have to deal with native dependencies in Xcode or Android Studio.</p>\n<p>Here is how you typically starts and run a new project with CNRA.</p>\n<pre><code>create-react-native-app MyCRNA\ncd MyCRNA\nyarn start\ni\n</code></pre>\n<p>You get your iOS simulator running within minutes from project initialization and are ready to start coding right away.</p>\n<p>While not having to open Xcode and run the project from there is nice, it's not the idea of using CRNA we are talking about here.</p>\n<p>It's worth mentioning that we can run new project on iOS simulator/Android emulator because of these commands, which available to any ways of creating the project.</p>\n<pre><code>react-native run-ios\nreact-native run-android\n</code></pre>\n<p>What we need to appreciate that the beauty of creating project with CRNA is we are able to leverage on Expo's cool features(from Expo SDK, included during initialization).</p>\n<p>Here's two of them that I am highlighting:</p>\n<ol>\n<li>Access to Expo SDK API</li>\n<li>Run project on real devices without hassle</li>\n</ol>\n<h4>1. Access to Expo SDK API</h4>\n<p>Notice the amount of work to add vector icon and MapView with Expo.</p>\n<ul>\n<li>Add icon</li>\n</ul>\n<p><img src=\"addIconCodeChanges.png\" alt=\"Test\"></p>\n<p>It relies</p>\n<p>What about expo kit?\nFor running in real devices</p>","frontmatter":{"date":"April 17, 2018","path":"/blog/crna-expo-react-native-init","title":"CRNA, Expo or react-native init for your next project","excerpt":"Create your project with the right tool"}}},"pathContext":{}}

